/* eslint-disable no-undef */

const HIGH_PRIORITY = 999;

/**
 *    
 *
 * @param {EventBus} eventBus
 *
 */
export default function SendingNotifications (eventBus) {
  function notify (key, params) {
    if (!Array.isArray(params)) {
      params = [params];
    }
    window.callProcEx(key, { '#Root': [{ items: params }] });
  }
  const postParamsDelete = (param) => {
    setTimeout(() => {
      notify('EndDeleteElement', param);
    }, 100);
  };
  function createParamsClick (event) {
    if (event.element && event.element.type) {
      const params = {
        type: event.element.type,
        id: event.element.id,
        dataAttrs: event.dataAttrs,
        detail: event.originalEvent.detail
      };
      if (event.element.type === 'connection') {
        if (event.element.source.type) {
          params.source = event.element.source.id;
        }
        if (event.element.target.type) {
          params.target = event.element.target.id;
        }
      } else if (event.element.type === 'shape') {
        const connection = {
          incoming: [],
          outgoing: []
        };
        event.element.incoming.forEach(({ id }) => connection.incoming.push(id));
        event.element.outgoing.forEach(({ id }) => connection.outgoing.push(id));
        params.connection = connection;
      }
      return params;
    }
  }

  const requireDataAttrs = ['BUTTON'];
  function createAdditionalData (event) {
    const originalTarget = event.originalEvent.composedPath()[0];
    if (requireDataAttrs.includes(originalTarget.nodeName)) {
      event.dataAttrs = {
        innerElement: {
          id: originalTarget.id,
          type: originalTarget.nodeName.toLowerCase()
        }
      };
    }
    return event;
  }

  eventBus.on(['commandStack.shape.create.preExecuted'], HIGH_PRIORITY, function (event) {
    notify('BeginCreateElement', RootDiagram.convertElementToJson(event.context.shape));
  });
  eventBus.on(['commandStack.shape.create.postExecuted'], HIGH_PRIORITY, function (event) {
    notify('EndCreateElement', RootDiagram.convertElementToJson(event.context.shape));
  });
  eventBus.on(['commandStack.connection.create.preExecuted'], HIGH_PRIORITY, function (event) {
    notify('BeginCreateElement', RootDiagram.convertElementToJson(event.context.connection));
  });
  eventBus.on(['commandStack.connection.create.postExecuted'], HIGH_PRIORITY, function (event) {
    notify('EndCreateElement', RootDiagram.convertElementToJson(event.context.connection));
  });
  eventBus.on(['commandStack.shape.delete.preExecute'], HIGH_PRIORITY, function (event) {
    notify('BeginDeleteElement', RootDiagram.convertElementToJson(event.context.shape));
  });
  eventBus.on(['commandStack.connection.delete.preExecute'], HIGH_PRIORITY, function (event) {
    notify('BeginDeleteElement', RootDiagram.convertElementToJson(event.context.connection));
  });
  eventBus.on(['commandStack.shape.delete.postExecuted'], HIGH_PRIORITY, function (event) {
    postParamsDelete(event.context.shape.id);
  });
  eventBus.on(['commandStack.connection.delete.postExecuted'], HIGH_PRIORITY, function (event) {
    postParamsDelete(event.context.connection.id);
  });
  eventBus.on([
    'element.labelChanged',
    'element.hintChanged'
  ], HIGH_PRIORITY, function (event) {
    notify('EndUpdateElement', RootDiagram.convertElementToJson(event.element));
  });
  eventBus.on([
    'commandStack.shape.move.preExecuted',
    'commandStack.shape.resize.preExecuted'
  ], HIGH_PRIORITY, function (event) {
    notify('BeginUpdateElement', RootDiagram.convertElementToJson(event.context.oldShape));
  });
  eventBus.on([
    'commandStack.shape.move.postExecuted',
    'commandStack.shape.resize.postExecuted'
  ], HIGH_PRIORITY, function (event) {
    notify('EndUpdateElement', RootDiagram.convertElementToJson(event.context.shape));
  });
  eventBus.on([
    'commandStack.connection.reconnect.preExecuted',
    'commandStack.connection.updateWaypoints.preExecuted',
    'commandStack.connection.layout.preExecuted'
  ], HIGH_PRIORITY, function (event) {
    if (!event.context.ignore) {
      notify('BeginUpdateElement', RootDiagram.convertElementToJson(event.context.oldConnection));
    }
  });
  eventBus.on([
    'commandStack.connection.reconnect.postExecuted',
    'commandStack.connection.updateWaypoints.postExecuted',
    'commandStack.connection.layout.postExecuted'
  ], HIGH_PRIORITY, function (event) {
    if (!event.context.ignore) {
      notify('EndUpdateElement', RootDiagram.convertElementToJson(event.context.connection));
    }
  });
  eventBus.on(['element.click'], HIGH_PRIORITY, function (event) {
    if (event.element && event.element.type) {
      notify('DoClickItem', createParamsClick(createAdditionalData(event)));
    }
  });
  eventBus.on(['elements.customCommand'], HIGH_PRIORITY, function (event) {
    if (event.command) {
      notify('DoCustomAction', event.command);
    }
  });
  eventBus.on(['elements.elementCommand'], HIGH_PRIORITY, function (event) {
    if (event.command) {
      notify('DoElementAction', {
        action: event.command,
        id: event.element.id
      });
    }
  });
}

SendingNotifications.$inject = ['eventBus'];
