import inherits from 'inherits';

import {
  assign
} from 'min-dash';

import BaseLayouter from 'diagram-js/lib/layout/BaseLayouter';

import {
  repairConnection,
  withoutRedundantPoints
} from 'diagram-js/lib/layout/ManhattanLayout';

import {
  getMid,
  getOrientation
} from 'diagram-js/lib/layout/LayoutUtil';

export default function CustomLayouter () {}

inherits(CustomLayouter, BaseLayouter);

CustomLayouter.prototype.layoutConnection = function (connection, hints) {
  if (!hints) {
    hints = {};
  }

  const source = hints.source || connection.source;
  const target = hints.target || connection.target;
  const waypoints = hints.waypoints || connection.waypoints;
  let connectionStart = hints.connectionStart;
  let connectionEnd = hints.connectionEnd;

  let manhattanOptions,
    updatedWaypoints;

  if (!connectionStart) {
    connectionStart = getConnectionDocking(waypoints && waypoints[0], source);
  }

  if (!connectionEnd) {
    connectionEnd = getConnectionDocking(waypoints && waypoints[waypoints.length - 1], target);
  }

  // layout all connection between flow elements h:h, except for
  // (1) outgoing of boundary events -> layout based on attach orientation and target orientation
  // (2) incoming/outgoing of gateways -> v:h for outgoing, h:v for incoming
  // (3) loops
  if (source === target) {
    manhattanOptions = {
      preferredLayouts: getLoopPreferredLayout(source, connection)
    };
  } else {
    manhattanOptions = {
      preferredLayouts: ['h:h']
    };
  }

  if (manhattanOptions) {
    manhattanOptions = assign(manhattanOptions, hints);

    updatedWaypoints = withoutRedundantPoints(repairConnection(
      source,
      target,
      connectionStart,
      connectionEnd,
      waypoints,
      manhattanOptions
    ));
  }

  return updatedWaypoints || [connectionStart, connectionEnd];
};

// helpers //////////

function getConnectionDocking (point, shape) {
  return point ? (point.original || point) : getMid(shape);
}
function getLoopPreferredLayout (source, connection) {
  const waypoints = connection.waypoints;

  const orientation = waypoints && waypoints.length && getOrientation(waypoints[0], source);

  if (orientation === 'top') {
    return ['t:r'];
  } else if (orientation === 'right') {
    return ['r:b'];
  } else if (orientation === 'left') {
    return ['l:t'];
  }

  return ['b:l'];
}
