import {
  assign
} from 'min-dash';
import {
  query as domQuery
} from 'min-dom';

/**
   *    Hint
   *
   * @param  {eventBus}  eventBus
   * @param  {canvas}  canvas
   * @param  {directEditing}  directEditing
   *
   */
export default function HintEdit (eventBus, canvas, directEditing, defaultParams) {
  this._canvas = canvas;
  this._eventBus = eventBus;
  this._defaultParams = defaultParams;

  directEditing.registerProvider(this);

  eventBus.on('element.hintEdit', function (event) {
    activateDirectEdit(event.element, event.edit);
  });

  eventBus.on('directEditing.complete', function (event, context) {
    delete context.active.element.edit;
  });

  eventBus.on([
    'autoPlace.start',
    'canvas.viewbox.changing',
    'drag.init',
    'element.mousedown',
    'popupMenu.open',
    'root.set'
  ], function (event) {
    if (directEditing.isActive()) {
      directEditing.complete();
    }
  });

  function activateDirectEdit (element, type) {
    element.edit = type;
    directEditing.activate(element);
  };
}
HintEdit.$inject = [
  'eventBus',
  'canvas',
  'directEditing',
  'defaultParams'
];

HintEdit.prototype.activate = function (element) {
  if (element.type !== 'shape' && element.type !== 'connection') {
    return;
  }
  if (element.edit !== 'hint') {
    return;
  }
  const options = {};
  const hint = element.hint || '';
  const defaultParams = this._defaultParams[element.type === 'connection' ? 'CONNECTION' : 'SHAPE'] || {};
  const defaultHint = defaultParams.hint || '';
  const defaultStyle = defaultParams.innerLabel.style || {};

  // text
  let text;
  if (hint) {
    text = hint;
  } else {
    text = defaultHint;
  }

  if (text === undefined) {
    return;
  }

  const context = {
    text: text,
    display: 'flex',
    alignItems: 'center'
  };

  let bbox = this._canvas.getAbsoluteBBox(element);
  if (element.type === 'connection') {
    bbox = { ...bbox, ...hint };
    // fix     
    if (!text) {
      bbox.y -= 10;
    }
    delete bbox.width;
    delete bbox.height;
  }
  context.bounds = bbox;

  if (element.type === 'shape') {
    options.centerVertically = true;
  }

  assign(context, {
    options: options,
    style: {
      ...defaultStyle
    }
  });
  return context;
};

HintEdit.prototype.update = function (element, newHint) {
  delete element.edit;
  element.hint = newHint;
  //   <title> svg 
  const hintEl = domQuery(`title[data-element-id=${element.id}]`);
  hintEl.textContent = newHint;

  this._eventBus.fire('element.hintChanged', { element: element });
};
