import BaseHtml from 'diagram-js/lib/util/Text';
import inherits from 'inherits';
import {
  create as svgCreate
} from 'tiny-svg';

export default function Html (config) {
  BaseHtml.call(this, config);
  Html.prototype.baseLayoutText = BaseHtml.prototype.layoutText;
}
inherits(Html, BaseHtml);

Html.prototype.createHtml = function (html, options) {
  return this.layoutHtml(html, options);
};

const getCssString = (style) => (
  Object.entries(style).map(([k, v]) => `${k}:${v}`).join(';')
);

const getCss = (styles) => {
  return Object.entries(styles).reduce((css, [query, nodeStyles]) => {
    return css + query + '{' + getCssString(nodeStyles) + '}';
  }, '');
};

Html.prototype.layoutHtml = function (html, options) {
  const defaultStyles = `
    body {
      height: ${options.size.height};
      width: ${options.size.width};
      margin: 0;
      overflow: hidden;
      pointer-events: auto;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      clip-path: url(#${options.clipId})
    }
    button * {
      pointer-events: none;
    }
    svg.svg-defs {
      position: absolute;
      height: 0;
      width: 0;
      opacity: 0;
    }
  `;

  const containerElement = document.createElement('div');
  containerElement.id = 'html-' + options.id;

  const containerSVG = svgCreate('foreignObject');
  containerSVG.style.width = options.size.width;
  containerSVG.style.height = options.size.height;

  const bodyElement = document.createElement('body');
  bodyElement.innerHTML = html;

  const shadowOpen = containerElement.attachShadow({ mode: 'open' });

  const styleElement = document.createElement('style');
  styleElement.innerText = defaultStyles + getCss(options.style.html);

  if (options.clipId && options.clipPath) {
    const defs = svgCreate('defs');
    defs.appendChild(options.clipPath);

    const clipSVG = svgCreate('svg', {
      class: 'svg-defs'
    });
    clipSVG.appendChild(defs);

    bodyElement.appendChild(clipSVG);
  }

  containerSVG.appendChild(containerElement);
  shadowOpen.appendChild(bodyElement);
  bodyElement.appendChild(styleElement);

  return containerSVG;
};
