/**
   *     
   *
   * @param  {eventBus}  eventBus
   *
   */
export default function Modal (eventBus) {
  this._eventBus = eventBus;

  eventBus.on('modal.open', function () {
    const modal = openModal();
    return modal;
  });

  eventBus.on('modal.close', function (_, modal) {
    closeModal(modal);
  });

  function closeModal (modal) {
    modal.style.display = 'none';
  }

  function openModal () {
    // <div class="modal-content">
    //   <div class="modal-header">
    //     <span class="close">&times;</span>
    //     <h2>Modal Header</h2>
    //   </div>
    //   <div class="modal-body">
    //     <p>Some text in the Modal Body</p>
    //     <p>Some other text...</p>
    //   </div>
    // </div>
    const modal = document.createElement('div');
    modal.className = 'modal';

    const modalContent = document.createElement('div');
    modalContent.className = 'modal-content';
    modalContent.style.overflow = 'auto';

    const modalHeader = document.createElement('div');
    modalHeader.className = 'modal-header';

    const modalControls = document.createElement('div');
    modalControls.className = 'modal-controls';

    const modalClose = document.createElement('span');
    modalClose.className = 'close';
    modalClose.innerHTML = '&times;';

    const modalBody = document.createElement('div');
    modalBody.className = 'modal-body';
    modalBody.style.overflow = 'scroll';

    modalClose.onclick = () => {
      modal.style.display = 'none';
    };
    window.onclick = (event) => {
      if (event.target === modal) {
        closeModal(modal);
      }
    };

    modal.style.display = 'block';

    modalHeader.appendChild(modalControls);
    modalHeader.appendChild(modalClose);
    modalContent.append(modalHeader, modalBody);
    modal.appendChild(modalContent);
    document.body.appendChild(modal);

    return { modal, header: modalHeader, body: modalBody, controls: modalControls };
  };
}

Modal.$inject = [
  'eventBus'
];
