import BasePalette from 'diagram-js/lib/features/palette/Palette';
import inherits from 'inherits';
import {
  classes as domClasses
} from 'min-dom';

/**
 *    
 * :
 *    
 * @param {EventBus} eventBus
 * @param {ElementRegistry} elementRegistry
 * @param {Styles} styles
 */
export default function Palette (eventBus, canvas) {
  BasePalette.call(this, eventBus, canvas);
}
inherits(Palette, BasePalette);

const PALETTE_PREFIX = 'djs-palette-';
const PALETTE_HIDDEN_CLS = 'hidden';

Palette.prototype.show = function () {
  const container = this._container;
  const cls = domClasses(container);
  cls.toggle(PALETTE_PREFIX + PALETTE_HIDDEN_CLS, false);
};

Palette.prototype.hide = function () {
  const container = this._container;
  const cls = domClasses(container);
  cls.toggle(PALETTE_PREFIX + PALETTE_HIDDEN_CLS, true);
};

Palette.$inject = [
  'eventBus',
  'canvas'
];
