const HIGH_PRIORITY = 1;

/**
 *    
 *
 * @param {EventBus} eventBus
 *
 */
export default function DisplayConnectHover (eventBus) {
  //        
  function updateElement (element, isHover) {
    if (isHover) {
      element.isHover = isHover;
    } else {
      delete element.isHover;
    }
    //    
    eventBus.fire('element.changed', { element: element });
  }

  function runUpdateElement (event, isHover) {
    if (event.element && event.element.type === 'connection') {
      updateElement(event.element, isHover);
    }
    if (event.element && event.element.type === 'shape') {
      event.element.incoming.forEach(function (connect) {
        updateElement(connect, isHover);
      });
      event.element.outgoing.forEach(function (connect) {
        updateElement(connect, isHover);
      });
      updateElement(event.element, isHover);
    }
  }
  //   
  eventBus.on(['element.hover'], HIGH_PRIORITY, function (event) {
    if (event.element && (event.element.type === 'connection' || (event.element.innerLabel && event.element.innerLabel.isHtml))) {
      return;
    }
    runUpdateElement(event, true);
  });
  eventBus.on(['element.out'], HIGH_PRIORITY, function (event) {
    if (event.element && (event.element.type === 'connection' || (event.element.innerLabel && event.element.innerLabel.isHtml))) {
      return;
    }
    runUpdateElement(event, false);
  });
}

DisplayConnectHover.$inject = ['eventBus'];
