import BaseResizeShapeHandler from 'diagram-js/lib/features/modeling/cmd/ResizeShapeHandler';
import inherits from 'inherits';
import {
  forEach
} from 'min-dash';
import {
  getResizedSourceAnchor,
  getResizedTargetAnchor
} from './helper/AnchorsHelper';

export default function ResizeShapeHandler (modeling) {
  BaseResizeShapeHandler.call(this, modeling);
}
inherits(ResizeShapeHandler, BaseResizeShapeHandler);

ResizeShapeHandler.$inject = ['modeling'];

ResizeShapeHandler.prototype.postExecute = function (context) {
  const modeling = this._modeling;

  const shape = context.shape;
  const oldBounds = context.oldBounds;
  const hints = context.hints || {};

  if (hints.layout === false) {
    return;
  }

  forEach(shape.incoming, function (c) {
    modeling.layoutConnection(c, {
      connectionEnd: getResizedTargetAnchor(c, shape, oldBounds)
    });
  });

  forEach(shape.outgoing, function (c) {
    modeling.layoutConnection(c, {
      connectionStart: getResizedSourceAnchor(c, shape, oldBounds)
    });
  });
};
