import BaseRenderer from 'diagram-js/lib/draw/BaseRenderer';
import inherits from 'inherits';
import Ids from 'ids';
import {
  isObject,
  assign
} from 'min-dash';
import {
  query as domQuery
} from 'min-dom';

import {
  append as svgAppend,
  attr as svgAttr,
  create as svgCreate,
  classes as svgClasses,
  replace as svgReplace
} from 'tiny-svg';
import { transform } from 'diagram-js/lib/util/SvgTransformUtil';
import { componentsToPath } from 'diagram-js/lib/util/RenderUtil';
import getDefs from '../util/Canvas';

const RENDERER_IDS = new Ids();

export default function CustomDraw (
  config, eventBus, styles,
  canvas, textRenderer, defaultParams, priority) {
  BaseRenderer.call(this, eventBus, priority);

  const rendererId = RENDERER_IDS.next();

  const markers = {};

  const computeStyle = styles.computeStyle;

  function addMarker (id, options) {
    const attrs = assign({
      fill: 'black',
      strokeWidth: 1,
      strokeLinecap: 'round',
      strokeDasharray: 'none'
    }, options.attrs);

    const ref = options.ref || { x: 0, y: 0 };

    const scale = options.scale || 1;

    // fix for safari / chrome / firefox bug not correctly
    // resetting stroke dash array
    if (attrs.strokeDasharray === 'none') {
      attrs.strokeDasharray = [10000, 1];
    }

    const marker = svgCreate('marker');

    svgAttr(options.element, attrs);

    svgAppend(marker, options.element);

    svgAttr(marker, {
      id: id,
      viewBox: '0 0 20 20',
      refX: ref.x,
      refY: ref.y,
      markerWidth: 20 * scale,
      markerHeight: 20 * scale,
      orient: 'auto-start-reverse'
    });

    const defs = getDefs(canvas);

    svgAppend(defs, marker);

    markers[id] = marker;
  }

  function colorEscape (str) {
    // only allow characters and numbers
    return str.replace(/[^0-9a-zA-z]+/g, '_');
  }
  function marker (marker, styleParent) {
    if (marker) {
      const type = marker.type;
      let markerStyle = {};
      if (marker.style) {
        markerStyle = marker.style;
      }
      const fill = markerStyle.fill || styleParent.fill || '';
      const fillOpacity = markerStyle.fillOpacity || styleParent.fillOpacity || '';
      const stroke = markerStyle.stroke || styleParent.stroke || '';
      const strokeWidth = markerStyle.strokeWidth || styleParent.strokeWidth || '';
      const strokeOpacity = markerStyle.strokeOpacity || styleParent.strokeOpacity || '';

      const id = type + '-' + colorEscape(fill) + '-' + colorEscape(stroke) + '-' + strokeOpacity + '-' + rendererId;

      if (!markers[id]) {
        createMarker(id, type, fill, stroke, strokeOpacity, fillOpacity, strokeWidth);
      }

      return 'url(#' + id + ')';
    }
  }
  function renderLabel (parentGfx, label, options, rect = false) {
    let content;
    if (options.isHtml) {
      const clipId = 'clipPath-' + options.id;
      const clipPath = svgCreate('clipPath', {
        id: clipId
      });

      svgAppend(clipPath, parentGfx.firstChild.cloneNode());

      options.clipId = clipId;
      options.clipPath = clipPath;

      content = textRenderer.createHtml(label || '', options);
    } else {
      content = textRenderer.createText(label || '', options);
    }

    svgClasses(content).add('djs-label');

    svgAppend(parentGfx, content);

    return content;
  }

  function renderEmbeddedLabel (parentGfx, element, align, defaultStyle, box = element) {
    const innerLabel = element.innerLabel || {};
    const isHtml = innerLabel.isHtml || false;
    const label = innerLabel.text || '';
    const style = isHtml ? { html: { ...innerLabel.style } } : innerLabel.style;
    const position = innerLabel.position || {};
    const size = { width: element.width, height: element.height };
    const id = element.id;

    return renderLabel(parentGfx, label, {
      box: box,
      align: align,
      position,
      size,
      isHtml,
      id,
      style: {
        fill: 'black',
        ...defaultStyle,
        ...style
      }
    });
  }

  function createMarker (id, type, fill, stroke, opacity, fillOpacity, strokeWidth) {
    if (type === 'arrow-fill') {
      const arrowFill = svgCreate('path');
      svgAttr(arrowFill, { d: 'M 1 5 L 11 10 L 1 15 Z' });

      addMarker(id, {
        element: arrowFill,
        ref: { x: 11, y: 10 },
        scale: 0.5,
        attrs: {
          fill: stroke,
          stroke: stroke,
          'stroke-width': strokeWidth,
          'stroke-opacity': opacity,
          'fill-opacity': fillOpacity
        }
      });
    }

    if (type === 'circle-fill') {
      const circleFill = svgCreate('circle');
      svgAttr(circleFill, { cx: 6, cy: 6, r: 3.5 });

      addMarker(id, {
        element: circleFill,
        attrs: {
          fill: fill,
          stroke: stroke,
          'stroke-width': strokeWidth,
          'stroke-opacity': opacity,
          'fill-opacity': fillOpacity
        },
        ref: { x: 6, y: 6 }
      });
    }

    if (type === 'circle') {
      const circle = svgCreate('circle');
      svgAttr(circle, { cx: 6, cy: 6, r: 3.5 });

      addMarker(id, {
        element: circle,
        attrs: {
          fill: fill,
          stroke: stroke,
          'stroke-width': strokeWidth,
          'stroke-opacity': opacity,
          'fill-opacity': fillOpacity
        },
        ref: { x: 6, y: 6 }
      });
    }

    if (type === 'arrow') {
      const arrow = svgCreate('path');
      svgAttr(arrow, { d: 'm 1 5 l 0 -3 l 7 3 l -7 3 z' });

      addMarker(id, {
        element: arrow,
        attrs: {
          fill: fill,
          stroke: stroke,
          strokeLinecap: 'butt',
          'stroke-width': strokeWidth,
          'stroke-opacity': opacity,
          'fill-opacity': fillOpacity
        },
        ref: { x: 8.5, y: 5 }
      });
    }

    if (type === 'arrow-check-inner') {
      const arrowCheckInner = svgCreate('path');
      svgAttr(arrowCheckInner, { d: 'M 11 5 L 1 10 L 11 15' });

      addMarker(id, {
        element: arrowCheckInner,
        attrs: {
          fill: 'none',
          stroke: stroke,
          strokeWidth: strokeWidth || 1.5,
          'stroke-opacity': opacity,
          'fill-opacity': fillOpacity
        },
        ref: { x: 12, y: 10 },
        scale: 0.5
      });
    }

    if (type === 'arrow-check') {
      const arrowCheck = svgCreate('path');
      svgAttr(arrowCheck, { d: 'M 1 5 L 11 10 L 1 15' });

      addMarker(id, {
        element: arrowCheck,
        attrs: {
          fill: fill,
          stroke: stroke,
          strokeWidth: strokeWidth || 1.5,
          'stroke-opacity': opacity,
          'fill-opacity': fillOpacity
        },
        ref: { x: 11, y: 10 },
        scale: 0.5
      });
    }

    if (type === 'diamond') {
      const diamond = svgCreate('path');
      svgAttr(diamond, { d: 'M 0 10 L 8 6 L 16 10 L 8 14 Z' });

      addMarker(id, {
        element: diamond,
        attrs: {
          fill: fill,
          stroke: stroke,
          'stroke-width': strokeWidth,
          'stroke-opacity': opacity,
          'fill-opacity': fillOpacity
        },
        ref: { x: 15, y: 10 },
        scale: 0.5
      });
    }
  }

  function drawPath (parentGfx, d, attrs) {
    attrs = computeStyle(attrs, ['no-fill'], {
      strokeWidth: 2,
      stroke: 'black'
    });

    const path = svgCreate('path');
    svgAttr(path, { d: d });
    svgAttr(path, attrs);

    svgAppend(parentGfx, path);

    return path;
  }

  function createPathFromConnection (connection) {
    const waypoints = connection.waypoints;

    let pathData = 'm  ' + waypoints[0].x + ',' + waypoints[0].y;
    for (let i = 1; i < waypoints.length; i++) {
      pathData += 'L' + waypoints[i].x + ',' + waypoints[i].y + ' ';
    }
    return pathData;
  }
  function getShowLabel (showLabel, defaultShowLabel, isHover) {
    switch (showLabel !== undefined ? showLabel : defaultShowLabel) {
      case 'hover':
        return isHover;
      case 'hoverOut':
        return !isHover;
      case false:
        return false;
      case true:
      default:
        return true;
    }
  }

  function createConnectionCustom (parentGfx, element, attrs) {
    const pathData = createPathFromConnection(element);

    attrs = {
      ...defaultParams.CONNECTION.style,
      ...element.style,
      ...attrs
    };

    if (element.isHover && (element.hoverStyle || defaultParams.CONNECTION.hoverStyle)) {
      attrs = element.hoverStyle ? { ...attrs, ...element.hoverStyle } : { ...attrs, ...defaultParams.CONNECTION.hoverStyle };
    }
    attrs.markerEnd = marker(element.markerEnd || defaultParams.CONNECTION.markerEnd, attrs);
    attrs.markerStart = marker(element.markerStart || defaultParams.CONNECTION.markerStart, attrs);

    const path = drawPath(parentGfx, pathData, attrs);
    if (element && getShowLabel(element.showLabel, defaultParams.CONNECTION.showLabel, element.isHover)) {
      const innerLabel = element.innerLabel || {};
      const position = innerLabel.position || {};
      const positionX = position.x || 0;
      const positionY = position.y || 0;
      const rect = drawRect(parentGfx, 0, 0, 0);
      const midPoint = path.getPointAtLength(path.getTotalLength() / 2);
      const label = renderEmbeddedLabel(parentGfx, element, 'left', defaultParams.CONNECTION.innerLabel.style, rect);
      const labelBounds = label.getBBox();
      const translateX = midPoint.x - labelBounds.width / 2 + positionX;
      const translateY = midPoint.y - labelBounds.height / 2 + positionY;
      //       
      element.innerLabel = { ...element.innerLabel, x: translateX, y: translateY, width: labelBounds.width, height: labelBounds.height };
      svgAttr(rect, { x: translateX, y: translateY, width: labelBounds.width, height: labelBounds.height, stroke: 'transparent' });

      transform(label, translateX, translateY, 0);
    }
    return path;
  }
  function drawRect (parentGfx, width, height, r, offset, attrs) {
    if (isObject(offset)) {
      attrs = offset;
      offset = 0;
    }

    offset = offset || 0;

    attrs = computeStyle(attrs, {
      stroke: 'black',
      strokeWidth: 2,
      fill: 'white'
    });

    const rect = svgCreate('rect');
    svgAttr(rect, {
      x: offset,
      y: offset,
      width: width - offset * 2,
      height: height - offset * 2,
      rx: r,
      ry: r
    });
    svgAttr(rect, attrs);

    svgAppend(parentGfx, rect);

    return rect;
  }
  function drawCircle (parentGfx, width, height, offset, attrs) {
    if (isObject(offset)) {
      attrs = offset;
      offset = 0;
    }

    offset = offset || 0;

    attrs = computeStyle(attrs, {
      stroke: 'black',
      strokeWidth: 2,
      fill: 'white'
    });

    if (attrs.fill === 'none') {
      delete attrs.fillOpacity;
    }

    const cx = width / 2;
    const cy = height / 2;

    const size = width > height ? height : width;
    const circle = svgCreate('circle');
    svgAttr(circle, {
      cx: cx,
      cy: cy,
      r: Math.round(size / 2 - offset)
    });
    svgAttr(circle, attrs);

    svgAppend(parentGfx, circle);

    return circle;
  }
  function drawDiamond (parentGfx, width, height, attrs) {
    const x_2 = width / 2;
    const y_2 = height / 2;

    const points = [{ x: x_2, y: 0 }, { x: width, y: y_2 }, { x: x_2, y: height }, { x: 0, y: y_2 }];

    const pointsString = points.map(function (point) {
      return point.x + ',' + point.y;
    }).join(' ');

    attrs = computeStyle(attrs, {
      stroke: 'black',
      strokeWidth: 2,
      fill: 'white'
    });

    const polygon = svgCreate('polygon');
    svgAttr(polygon, {
      points: pointsString
    });
    svgAttr(polygon, attrs);

    svgAppend(parentGfx, polygon);

    return polygon;
  }

  function renderRect (parentGfx, element, attrs) {
    attrs = attrs || {};

    if (!('fillOpacity' in attrs)) {
      attrs.fillOpacity = 0.95;
    }
    if (element.isFrame) {
      attrs.strokeDasharray = 4;
    }

    return drawRect(parentGfx, element.width, element.height, 10, attrs);
  }

  const customElement = {
    circle: function (parentGfx, element, attrs) {
      return drawCircle(parentGfx, element.width, element.height, 0, attrs);
    },
    diamond: function (parentGfx, element, attrs) {
      return drawDiamond(parentGfx, element.width, element.height, attrs);
    },
    default: function (parentGfx, element, attrs) {
      return renderRect(parentGfx, element, attrs);
    }
  };
  function drawShapeCustom (parentGfx, element) {
    let attrs = {
      fill: 'transparent',
      stroke: 'black',
      ...defaultParams.SHAPE.style,
      ...element.style
    };
    if (element.isHover && (element.hoverStyle || defaultParams.SHAPE.hoverStyle)) {
      attrs = element.hoverStyle ? { ...attrs, ...element.hoverStyle } : { ...attrs, ...defaultParams.SHAPE.hoverStyle };
    }

    const subType = element.subType || 'default';
    const gfx = customElement[subType](parentGfx, element, attrs);

    if (element &&
        element.innerLabel &&
        element.innerLabel.text &&
        getShowLabel(element.showLabel, defaultParams.SHAPE.showLabel, element.isHover)) {
      renderEmbeddedLabel(parentGfx, element, 'center-middle', defaultParams.SHAPE.innerLabel.style);
    }

    return gfx;
  }

  this.createConnectionCustom = createConnectionCustom;
  this.drawShapeCustom = drawShapeCustom;
}

inherits(CustomDraw, BaseRenderer);

CustomDraw.$inject = [
  'config',
  'eventBus',
  'styles',
  'canvas',
  'textRenderer',
  'defaultParams'
];
CustomDraw.prototype.canRender = function (element) {
  return !element.hidden;
};

CustomDraw.prototype.drawConnection = function drawConnection (parentGfx, element) {
  return this.createConnectionCustom(parentGfx, element);
};
CustomDraw.prototype.drawShape = function drawShape (parentGfx, element) {
  return this.drawShapeCustom(parentGfx, element);
};

export function getDiamondPath (shape) {
  const width = shape.width;
  const height = shape.height;
  const x = shape.x;
  const y = shape.y;
  const halfWidth = width / 2;
  const halfHeight = height / 2;

  const diamondPath = [
    ['M', x + halfWidth, y],
    ['l', halfWidth, halfHeight],
    ['l', -halfWidth, halfHeight],
    ['l', -halfWidth, -halfHeight],
    ['z']
  ];

  return componentsToPath(diamondPath);
}

export function getRectPath (shape) {
  const x = shape.x;
  const y = shape.y;
  const width = shape.width;
  const height = shape.height;

  const rectPath = [
    ['M', x, y],
    ['l', width, 0],
    ['l', 0, height],
    ['l', -width, 0],
    ['z']
  ];

  return componentsToPath(rectPath);
}

export function getCirclePath (shape) {
  const offset = shape.offset || 0;
  const cx = shape.x + shape.width / 2;
  const cy = shape.y + shape.height / 2;
  const radius = Math.round((shape.width + shape.height) / 4 - offset);

  const circlePath = [
    ['M', cx, cy],
    ['m', 0, -radius],
    ['a', radius, radius, 0, 1, 1, 0, 2 * radius],
    ['a', radius, radius, 0, 1, 1, 0, -2 * radius],
    ['z']
  ];

  return componentsToPath(circlePath);
}

CustomDraw.prototype.getShapePath = function (element) {
  if (element.subType === 'diamond') {
    return getDiamondPath(element);
  }
  if (element.subType === 'circle') {
    return getCirclePath(element);
  }

  return getRectPath(element);
};
