import {
  assign
} from 'min-dash';

/**
 *    Label
 *
 * @param  {eventBus}  eventBus
 * @param  {canvas}  canvas
 * @param  {directEditing}  directEditing
 *
 */
export default function LabelEdit (eventBus, canvas, directEditing, defaultParams) {
  this._canvas = canvas;
  this._eventBus = eventBus;
  this._defaultParams = defaultParams;

  directEditing.registerProvider(this);

  eventBus.on('element.dblclick', function (event) {
    const element = event.element;
    if (element.editable === false || (element.type !== 'shape' && element.type !== 'connection')) {
      return;
    }
    if (element.innerLabel && element.innerLabel.isHtml) {
      eventBus.fire('element.codeEdit', element);
    } else {
      activateDirectEdit(element);
    }
  });

  eventBus.on([
    'autoPlace.start',
    'canvas.viewbox.changing',
    'drag.init',
    'element.mousedown',
    'popupMenu.open',
    'root.set'
  ], function (event) {
    if (directEditing.isActive()) {
      directEditing.complete();
    }
  });

  function activateDirectEdit (element) {
    directEditing.activate(element);
  }
}
LabelEdit.$inject = [
  'eventBus',
  'canvas',
  'directEditing',
  'defaultParams'
];

LabelEdit.prototype.activate = function (element) {
  const options = {};
  const innerLabel = element.innerLabel || {};
  const style = innerLabel.style || {};
  const defaultParams = this._defaultParams[element.type === 'connection' ? 'CONNECTION' : 'SHAPE'] || {};
  const defaultLabel = defaultParams.innerLabel || {};
  const defaultStyle = defaultLabel.style || {};

  // text
  let text;
  if (innerLabel && innerLabel.text) {
    text = innerLabel.text;
  } else {
    text = '';
  }

  if (text === undefined) {
    return;
  }

  const context = {
    text: text,
    display: 'flex',
    alignItems: 'center'
  };

  let bbox = this._canvas.getGraphics(element).getBoundingClientRect();
  if (element.type === 'connection') {
    bbox = {
      x: bbox.left,
      y: bbox.top
    };
    // fix     
    if (!text) {
      bbox.y -= 10;
    }
    delete bbox.width;
    delete bbox.height;
  }
  context.bounds = bbox;

  if (element.type === 'shape') {
    options.centerVertically = true;
  }

  assign(context, {
    options: options,
    style: {
      ...defaultStyle,
      ...style
    }
  });
  return context;
};

LabelEdit.prototype.update = function (element, newLabel) {
  if (element.innerLabel && element.innerLabel.text) {
    element.innerLabel.text = newLabel;
  } else {
    element.innerLabel = { text: newLabel };
  }

  element.innerLabel.text = newLabel;
  this._eventBus.fire('element.labelChanged', { element: element });
  this._eventBus.fire('element.changed', { element: element });
};
