import BaseText from 'diagram-js/lib/util/Text';
import inherits from 'inherits';

export default function Text (config) {
  BaseText.call(this, config);
  Text.prototype.baseLayoutText = BaseText.prototype.layoutText;
}
inherits(Text, BaseText);

Text.prototype.layoutText = function (text, options) {
  const position = options.position;
  const textBox = this.baseLayoutText(text, options);

  if (!position.x && !position.y) {
    return textBox;
  }

  const tspan = textBox.element.firstChild;
  const tspanX = Number(tspan.getAttribute('x'));
  const tspanY = Number(tspan.getAttribute('y'));

  tspan.setAttribute('x', `${tspanX + (position.x || 0)}`);
  tspan.setAttribute('y', `${tspanY + (position.y || 0)}`);

  return textBox;
};
