/**
 *      
 */
export default function ElementCommandsMenuProvider (popupMenu, eventBus) {
  this._popupMenu = popupMenu;
  this._eventBus = eventBus;

  this.register();
}

ElementCommandsMenuProvider.$inject = ['popupMenu', 'eventBus'];

/**
 *   
 *
 * @param {djs.model.Base} element
 */
ElementCommandsMenuProvider.prototype.getEntries = function (element) {
  const entries = element.commands.map(({ label, action }, index) => {
    return {
      label: label,
      id: element.id + '-command-' + index,
      action: () => {
        this._eventBus.fire('elements.elementCommand', { command: action, element });
      }
    };
  });

  return entries || [];
};

/**
 *   .
 *
 * @param  {djs.model.Base} element
 */
ElementCommandsMenuProvider.prototype.getHeaderEntries = function (element) {
  return [];
};

/**
 *    id.
 */
ElementCommandsMenuProvider.prototype.register = function () {
  this._popupMenu.registerProvider('element-commands-menu', this);
};
