import BaseModeling from 'diagram-js/lib/features/modeling/Modeling';
import inherits from 'inherits';
import CopyElementsHandler from './cmd/CopyElementsHandler';
import DeleteShapeHandler from './cmd/DeleteShapeHandler';
import ResizeShapeHandler from './cmd/ResizeShapeHandler';
import ReconnectConnectionHandler from './cmd/ReconnectConnectionHandler';

/**
 *     
 * :
 *      
 *         
 * @param {EventBus} eventBus
 * @param {ElementRegistry} elementRegistry
 * @param {Styles} styles
 */
export default function Modeling (eventBus, elementFactory, commandStack) {
  BaseModeling.call(this, eventBus, elementFactory, commandStack);
  Modeling.prototype.baseGetHandlers = BaseModeling.prototype.getHandlers;
}
inherits(Modeling, BaseModeling);

Modeling.$inject = [
  'eventBus',
  'elementFactory',
  'commandStack'
];

const overrideHandlers = {
  'element.copy': CopyElementsHandler,

  'shape.resize': ResizeShapeHandler,
  'shape.delete': DeleteShapeHandler,

  'connection.reconnect': ReconnectConnectionHandler
};

/**
 *     ,        .
 *
 * @param {Array<djs.mode.Base>} shapes
 * @param {Point} delta
 * @param {djs.model.Base} [target]
 * @param {Object} [hints]
 * @param {boolean} [hints.attach=false]
 */
Modeling.prototype.moveElements = function (shapes, delta, target, hints) {
  hints = hints || {};

  const attach = hints.attach;

  let newParent = (target.isFrame || target.type === 'root') && target;
  let newHost;

  if (attach === true) {
    newHost = target;
    newParent = target.parent;
  } else

  if (attach === false) {
    newHost = null;
  }

  const context = {
    shapes: shapes,
    delta: delta,
    newParent: newParent,
    newHost: newHost,
    hints: hints
  };

  this._commandStack.execute('elements.move', context);
};

Modeling.prototype.moveShape = function (shape, delta, newParent, newParentIndex, hints) {
  if (typeof newParentIndex === 'object') {
    hints = newParentIndex;
    newParentIndex = null;
  }

  const context = {
    shape: shape,
    oldShape: Object.assign({}, shape),
    delta: delta,
    newParent: newParent,
    newParentIndex: newParentIndex,
    hints: hints || {}
  };

  this._commandStack.execute('shape.move', context);
};

Modeling.prototype.resizeShape = function (shape, newBounds, minBounds, hints) {
  const context = {
    shape: shape,
    oldShape: Object.assign({}, shape),
    newBounds: newBounds,
    minBounds: minBounds,
    hints: hints
  };

  this._commandStack.execute('shape.resize', context);
};

Modeling.prototype.layoutConnection = function (connection, hints, ignore) {
  const context = {
    ignore,
    connection: connection,
    hints: hints || {}
  };

  this._commandStack.execute('connection.layout', context);
};

Modeling.prototype.updateWaypoints = function (connection, newWaypoints, hints) {
  const context = {
    connection: connection,
    oldConnection: Object.assign({}, connection, { source: connection.source, target: connection.target }),
    newWaypoints: newWaypoints,
    hints: hints || {}
  };

  this._commandStack.execute('connection.updateWaypoints', context);
};

Modeling.prototype.getHandlers = function () {
  return {
    ...this.baseGetHandlers(),
    ...overrideHandlers
  };
};
