import inherits from 'inherits';

import RuleProvider from 'diagram-js/lib/features/rules/RuleProvider';


export default function ExampleRuleProvider (eventBus) {
  RuleProvider.call(this, eventBus);
}

ExampleRuleProvider.$inject = ['eventBus'];

inherits(ExampleRuleProvider, RuleProvider);

ExampleRuleProvider.prototype.init = function () {
  this.addRule('shape.create', function (context) {
    const target = context.target;
    const shape = context.shape;

    return target.parent === shape.target;
  });

  this.addRule(['connection.create', 'connection.layout', 'connection.reconnect'], 15000, function (context) {
    const source = context.source;
    const target = context.target;
    if((target && (target.type === 'connection' || target.type === 'root'))
        || (source && (source.type === 'connection' || source.type === 'root'))){
      return false
    }
    return source.parent === target.parent;
  });

  this.addRule('shape.resize', function (context) {
    return context.shape && context.shape.type === 'shape' ;
  });
};
