import CustomConnectSnapping from './CustomConnectSnapping';
import CustomCreateMoveSnapping from './CustomCreateMoveSnapping';
import SnappingModule from 'diagram-js/lib/features/snapping';

export default {
  __depends__: [ SnappingModule ],
  __init__: [
    'connectSnapping',
    'createMoveSnapping'
  ],
  connectSnapping: [ 'type', CustomConnectSnapping ],
  createMoveSnapping: [ 'type', CustomCreateMoveSnapping ]
};