/**
 * A example palette provider.
 */
export default function ExamplePaletteProvider (create, elementFactory, lassoTool, palette) {
  this._create = create;
  this._elementFactory = elementFactory;
  this._lassoTool = lassoTool;
  this._palette = palette;
  this._dynamicEntries = {};

  palette.registerProvider(this);
}

ExamplePaletteProvider.$inject = [
  'create',
  'elementFactory',
  'lassoTool',
  'palette'
];

/**
 *       
 * @param {array} newEntries
 */
ExamplePaletteProvider.prototype.addPaletteEntries = function (newEntries) {
  Object.assign(this._dynamicEntries, newEntries);
};

ExamplePaletteProvider.prototype.getPaletteEntries = function () {
  const create = this._create;
  const elementFactory = this._elementFactory;
  const lassoTool = this._lassoTool;
  const palette = this._palette;

  return Object.assign({
    'close-menu': {
      group: 'settings',
      className: 'palette-icon-close-menu',
      title: ' ',
      action: {
        click: function () {
          palette.close();
        }
      }
    },
    'settings-separator': {
      group: 'settings-separator',
      separator: true
    },
    'lasso-tool': {
      group: 'tools',
      className: 'palette-icon-lasso-tool',
      title: ' ',
      action: {
        click: function (event) {
          lassoTool.activateSelection(event);
        }
      }
    },
    'tool-separator': {
      group: 'tools-separator',
      separator: true
    },
    'create-shape': {
      group: 'create',
      className: 'palette-icon-create-shape',
      title: ' ',
      action: {
        click: function (event) {
          const shape = elementFactory.createShape({
            type: 'shape',
            width: 100,
            height: 80
          });

          create.start(event, shape);
        }
      }
    },
    'create-circle': {
      group: 'create',
      className: 'palette-icon-create-circle',
      title: ' ',
      action: {
        click: function (event) {
          const shape = elementFactory.createShape({
            type: 'shape',
            subType: 'circle',
            width: 80,
            height: 80
          });

          create.start(event, shape);
        }
      }
    },
    'create-diamond': {
      group: 'create',
      className: 'palette-icon-create-diamond',
      title: ' ',
      action: {
        click: function (event) {
          const shape = elementFactory.createShape({
            type: 'shape',
            subType: 'diamond',
            width: 80,
            height: 80
          });

          create.start(event, shape);
        }
      }
    },
    'create-frame': {
      group: 'create',
      className: 'palette-icon-create-frame',
      title: ' ',
      action: {
        click: function (event) {
          const shape = elementFactory.createShape({
            type: 'shape',
            width: 300,
            height: 200,
            isFrame: true
          });

          create.start(event, shape);
        }
      }
    }
  }, this._dynamicEntries);
};
