import BaseInteractionEvents from 'diagram-js/lib/features/interaction-events/InteractionEvents';
import inherits from 'inherits';

import {
  forEach
} from 'min-dash';

import {
  queryAll as domQueryAll
} from 'min-dom';

import {
  remove as svgRemove
} from 'tiny-svg';

/**
 *     
 * :
 *      
 *         
 * @param {EventBus} eventBus
 * @param {ElementRegistry} elementRegistry
 * @param {Styles} styles
 */
const LOW_PRIORITY = 500;

export default function InteractionEvents (eventBus, elementRegistry, styles) {
  BaseInteractionEvents.call(this, eventBus, elementRegistry, styles);
  InteractionEvents.prototype.createWaypointsHit = BaseInteractionEvents.prototype.createWaypointsHit;
  InteractionEvents.prototype.createBoxHit = BaseInteractionEvents.prototype.createBoxHit;

  /**
   *      
   *
   * @param {djs.model.Base} element
   * @param {SVGElement} gfx
   *
   * @return {SVGElement} created hit
   */
  this.createDefaultHit = function (element, gfx) {
    const waypoints = element.waypoints;
    const isShape = element.type === 'shape';
    const isHtml = element.innerLabel && element.innerLabel.isHtml && !element.isFrame;
    let boxType;

    if (waypoints) {
      return this.createWaypointsHit(gfx, waypoints);
    } else {
    /**
     * 'stroke' -  
     * 'all' -   
     */
      boxType = isShape && !isHtml ? 'all' : 'stroke';

      return this.createBoxHit(gfx, boxType, {
        width: element.width,
        height: element.height
      });
    }
  };

  /**
   *    .
   *
   * @param {SVGElement} gfx
   */
  this.removeHits = function (gfx) {
    const hits = domQueryAll('.djs-hit', gfx);

    forEach(hits, svgRemove);
  };

  eventBus.on('interactionEvents.removeHit', LOW_PRIORITY, (event) => {
    this.removeHits(event.gfx);
  });
}
inherits(InteractionEvents, BaseInteractionEvents);

InteractionEvents.$inject = [
  'eventBus',
  'elementRegistry',
  'styles'
];
