import BaseDeleteShapeHandler from 'diagram-js/lib/features/modeling/cmd/DeleteShapeHandler';
import inherits from 'inherits';
import {
  indexOf as collectionIdx
} from '../../../util/Collections';
import getDefs from '../../../util/Canvas';
import {
  query as domQuery
} from 'min-dom';
import {
  remove as svgRemove
} from 'tiny-svg';

/**
 *    
 * :
 *     clipPath,    
 * @param {canvas} canvas
 * @param {modeling} modeling
 */
export default function DeleteShapeHandler (canvas, modeling) {
  BaseDeleteShapeHandler.call(this, canvas, modeling);
}
inherits(DeleteShapeHandler, BaseDeleteShapeHandler);

/**
 *      
 */
DeleteShapeHandler.prototype.execute = function (context) {
  const canvas = this._canvas;

  const shape = context.shape;
  const oldParent = shape.parent;

  context.oldParent = oldParent;
  context.oldParentIndex = collectionIdx(oldParent.children, shape);

  canvas.removeShape(shape);

  if (shape.innerLabel && shape.innerLabel.isHtml) {
    //  clipPath 
    const defs = getDefs(canvas);
    const clipPath = domQuery('defs #clipPath-' + shape.id, defs);
    clipPath && svgRemove(clipPath);
  }

  return shape;
};

DeleteShapeHandler.$inject = ['canvas', 'modeling'];
