import {
  append as svgAppend,
  create as svgCreate
} from 'tiny-svg';
import {
  query as domQuery
} from 'min-dom';

/**
 *    svg  
 * @returns  defs
 */
export default function getDefs (canvas) {
  let defs = domQuery('defs', canvas._svg);

  if (!defs) {
    defs = svgCreate('defs');

    svgAppend(canvas._svg, defs);
  }
  return defs;
}
