import BaseReconnectConnectionHandler from 'diagram-js/lib/features/modeling/cmd/ReconnectConnectionHandler';
import inherits from 'inherits';
import { isArray } from 'min-dash';

/**
 *     
 * :
 *     connect.layout
 * @param {modeling} modeling
 */
export default function ReconnectConnectionHandler (modeling) {
  BaseReconnectConnectionHandler.call(this, modeling);
}
inherits(ReconnectConnectionHandler, BaseReconnectConnectionHandler);

ReconnectConnectionHandler.$inject = ['modeling'];

ReconnectConnectionHandler.prototype.preExecute = function (context) {
  context.oldConnection = Object.assign({}, context.connection, { source: context.connection.source, target: context.connection.target });
};

ReconnectConnectionHandler.prototype.postExecute = function (context) {
  const connection = context.connection;
  const newSource = context.newSource;
  const newTarget = context.newTarget;
  const dockingOrPoints = context.dockingOrPoints;
  const hints = context.hints || {};

  const layoutConnectionHints = {};

  if (hints.connectionStart) {
    layoutConnectionHints.connectionStart = hints.connectionStart;
  }

  if (hints.connectionEnd) {
    layoutConnectionHints.connectionEnd = hints.connectionEnd;
  }

  if (hints.layoutConnection === false) {
    return;
  }

  if (newSource && (!newTarget || hints.docking === 'source')) {
    layoutConnectionHints.connectionStart =
      layoutConnectionHints.connectionStart ||
      getDocking(
        isArray(dockingOrPoints) ? dockingOrPoints[0] : dockingOrPoints
      );
  }

  if (newTarget && (!newSource || hints.docking === 'target')) {
    layoutConnectionHints.connectionEnd =
      layoutConnectionHints.connectionEnd ||
      getDocking(
        isArray(dockingOrPoints)
          ? dockingOrPoints[dockingOrPoints.length - 1]
          : dockingOrPoints
      );
  }

  if (hints.newWaypoints) {
    layoutConnectionHints.waypoints = hints.newWaypoints;
  }

  //   
  this._modeling.layoutConnection(connection, layoutConnectionHints, true);
};

// helpers //////////

function getDocking (point) {
  return point.original || point;
}
