import BaseGraphicsFactory from 'diagram-js/lib/core/GraphicsFactory';
import inherits from 'inherits';
import {
  append as svgAppend,
  create as svgCreate
} from 'tiny-svg';

/**
 *      svg
 * :
 *   <title>    
 * @param {EventBus} eventBus
 * @param {ElementRegistry} elementRegistry
 * @param {DefaultParams} defaultParams
 */
export default function GraphicsFactory (eventBus, elementRegistry, defaultParams) {
  BaseGraphicsFactory.call(this, eventBus, elementRegistry);
  GraphicsFactory.prototype._baseCreateContainer = BaseGraphicsFactory.prototype._createContainer;
  GraphicsFactory.prototype._getChildrenContainer = BaseGraphicsFactory.prototype._getChildrenContainer;
  this.defaultParams = defaultParams;
}
inherits(GraphicsFactory, BaseGraphicsFactory);

GraphicsFactory.$inject = [
  'eventBus',
  'elementRegistry',
  'defaultParams'
];

GraphicsFactory.prototype.create = function (type, element, parentIndex) {
  const childrenGfx = this._getChildrenContainer(element.parent);
  return this._createContainer(type, childrenGfx, parentIndex, !!(element && element.isFrame), element);
};

GraphicsFactory.prototype._createContainer = function (type, childrenGfx, parentIndex, isFrame, element) {
  const gfx = this._baseCreateContainer(type, childrenGfx, parentIndex, isFrame);
  const defaultHint = this.defaultParams[type.toUpperCase()].hint;
  if (element) {
    const text = svgCreate('title');
    text.dataset.elementId = element.id;
    text.textContent = element.hint || defaultHint;
    svgAppend(gfx, text);
  }

  return gfx;
};
