import BasePopupMenu from 'diagram-js/lib/features/popup-menu/PopupMenu';
import inherits from 'inherits';

/**
 *    
 * :
 *        
 * @param {EventBus} eventBus
 * @param {ElementRegistry} elementRegistry
 * @param {Styles} styles
 */
export default function PopupMenu (config, eventBus, canvas) {
  BasePopupMenu.call(this, config, eventBus, canvas);
  this._eventBus = eventBus;
}
inherits(PopupMenu, BasePopupMenu);

PopupMenu.$inject = [
  'config.popupMenu',
  'eventBus',
  'canvas'
];

const CLOSE_EVENTS = [
  'element.mousedown',
  'elements.customCommand',
  'elements.elementCommand',
  'contextPad.close',
  'canvas.viewbox.changing',
  'commandStack.changed'
];

/**
 *         .
 */
PopupMenu.prototype._bindAutoClose = function () {
  this._eventBus.once(CLOSE_EVENTS, this.close, this);
};
