import BasePreviewSupport from 'diagram-js/lib/features/preview-support/PreviewSupport';
import { getVisual } from 'diagram-js/lib/util/GraphicsUtil';
import inherits from 'inherits';
import {
  append as svgAppend,
  attr as svgAttr,
  clone as svgClone
} from 'tiny-svg';

/**
 *   
 * :
 *  ,     connection      
 * @param {ElementRegistry} elementRegistry
 * @param {EventBus} eventBus
 * @param {Canvas} canvas
 * @param {Styles} styles
 */
export default function PreviewSupport (elementRegistry, eventBus, canvas, styles) {
  BasePreviewSupport.call(this, elementRegistry, eventBus, canvas, styles);
}
inherits(PreviewSupport, BasePreviewSupport);

PreviewSupport.$inject = [
  'elementRegistry',
  'eventBus',
  'canvas',
  'styles'
];

PreviewSupport.prototype.addDragger = function (element, group, gfx) {
  gfx = gfx || this.getGfx(element);

  const dragger = svgClone(gfx);
  const bbox = gfx.getBoundingClientRect();

  if (element.type === 'connection' &&
        element.initialSource !== element.initialTarget) {
    this._cloneMarkers(getVisual(dragger));
  }

  svgAttr(dragger, this._styles.cls('djs-dragger', [], {
    x: bbox.top,
    y: bbox.left
  }));

  svgAppend(group, dragger);

  return dragger;
};
