import inherits from 'inherits';

import CommandInterceptor from 'diagram-js/lib/command/CommandInterceptor';

export default function ResizeInterceptor (eventBus) {
  CommandInterceptor.call(this, eventBus);

  this.preExecute('shape.resize', ({ context }) => {
    const { shape, newBounds } = context;
    if (shape.subType === 'circle') {
      const size = (
        shape.width >= newBounds.width &&
      shape.height >= newBounds.height
          ? Math.min
          : Math.max)(newBounds.width, newBounds.height);
      context.newBounds.width = size;
      context.newBounds.height = size;
    }
  });
}

ResizeInterceptor.$inject = ['eventBus'];

inherits(ResizeInterceptor, CommandInterceptor);
