import Diagram from 'diagram-js/lib/Diagram';
import CustomDraw from '../lib/core';

import ConnectModule from 'diagram-js/lib/features/connect';
import ConnectionPreviewModule from 'diagram-js/lib/features/connection-preview';
import BendpointsModule from 'diagram-js/lib/features/bendpoints';
import ContextPadModule from 'diagram-js/lib/features/context-pad';
import CreateModule from 'diagram-js/lib/features/create';
import LassoToolModule from 'diagram-js/lib/features/lasso-tool';
import ModelingModule from 'diagram-js/lib/features/modeling';
import MoveCanvasModule from 'diagram-js/lib/navigation/movecanvas';
import MoveModule from 'diagram-js/lib/features/move';
import OutlineModule from 'diagram-js/lib/features/outline';
import PaletteModule from 'diagram-js/lib/features/palette';
import ResizeModule from 'diagram-js/lib/features/resize';
import RulesModule from 'diagram-js/lib/features/rules';
import SelectionModule from 'diagram-js/lib/features/selection';
import ZoomScrollModule from 'diagram-js/lib/navigation/zoomscroll';
import AlignElementsModule from 'diagram-js/lib/features/align-elements';
import KeyboardMoveSelectionModule from 'diagram-js/lib/features/keyboard-move-selection';
import KeyboardMoveModule from 'diagram-js/lib/navigation/keyboard-move';

import ProvidersModule from './providers';
import DisplayConnectHover from '../lib/features/DisplayConnectHover';
import LabelEdit from '../lib/features/label-editing';
import OnChangedZoomAndOffset from '../lib/features/OnChangedZoomAndOffset';
import SendingNotifications from '../lib/features/SendingNotifications';
import SnappingModule from '../lib/features/snapping';
import HintEdit from '../lib/features/HintEditing';
import CodeEdit from '../lib/features/CodeEditing';
import InteractionEvents from '../lib/features/InteractionEvents';
import ImageUpload from '../lib/features/ImageUpload';
import Modal from '../lib/features/Modal';
import Code from '../lib/features/Code';
import CopyPaste from '../lib/features/CopyPaste';
import HoverFix from '../lib/features/HoverFix';
import PopupMenu from '../lib/features/PopupMenu';
import Resize from '../lib/features/Resize';
import PreviewSupport from '../lib/features/PreviewSupport';
import Palette from '../lib/features/Palette';

/**
 * Our editor constructor
 *
 * @param { { container: Element, additionalModules?: Array<any> } } options
 *
 * @return {Diagram}
 */
export default function Editor (options) {
  const {
    container,
    additionalModules = []
  } = options;

  // default modules provided by the toolbox
  const builtinModules = [
    BendpointsModule,
    ConnectModule,
    ContextPadModule,
    CreateModule,
    LassoToolModule,
    ModelingModule,
    MoveCanvasModule,
    MoveModule,
    OutlineModule,
    PaletteModule,
    ResizeModule,
    RulesModule,
    SelectionModule,
    ZoomScrollModule,
    ConnectionPreviewModule,
    AlignElementsModule,
    KeyboardMoveSelectionModule,
    KeyboardMoveModule,
    HoverFix,
    CopyPaste
  ];

  // our own modules, contributing controls, customizations, and more
  const customModules = [
    InteractionEvents,
    ProvidersModule,
    CustomDraw,
    DisplayConnectHover,
    ImageUpload,
    Modal,
    Code,
    HintEdit,
    LabelEdit,
    CodeEdit,
    PopupMenu,
    OnChangedZoomAndOffset,
    SendingNotifications,
    SnappingModule,
    Resize,
    PreviewSupport,
    Palette
  ];

  return new Diagram({
    canvas: {
      container
    },
    modules: [
      ...builtinModules,
      ...customModules,
      ...additionalModules
    ]
  });
}
