const HIGH_PRIORITY = 1;

/**
 *         
 *
 * @param {EventBus} eventBus
 *
 */
export default function OnChangedZoomAndOffset (eventBus) {
  eventBus.on(['canvas.viewbox.changed'], HIGH_PRIORITY, function (event) {
    const viewbox = event.viewbox;
    window.callProcEx('UpdateZoomAndOffsetStore', { '#Root': [{ x: viewbox.x, y: viewbox.y, scale: viewbox.scale }] });
  });
}

OnChangedZoomAndOffset.$inject = ['eventBus'];
