/**
 *    
 * @param  {eventBus}  eventBus
 * @param  {elementRegistry}  elementRegistry
 */
export default function CodeEdit (eventBus, elementRegistry) {
  this._eventBus = eventBus;
  this._elementRegistry = elementRegistry;

  eventBus.on('element.codeEdit', function (_, element) {
    const { editor, container } = eventBus.fire(
      'code.open',
      element.id,
      element.innerLabel && element.innerLabel.text
    );
    const { modal, controls } = container;

    const editorSave = document.createElement('button');
    editorSave.innerText = '';
    editorSave.className = 'cm-editor-save';
    editorSave.onclick = () => {
      eventBus.fire('codeEdit.save', element, editor.state.doc.toString());
      eventBus.fire('code.close', modal);
    };

    const editorLoadImg = document.createElement('button');
    editorLoadImg.innerText = ' ';
    editorLoadImg.className = 'cm-editor-img';
    editorLoadImg.onclick = () => {
      eventBus.fire('element.imageUpload', (fileReader) => {
        editor.dispatch({
          changes: {
            from: editor.state.selection.main.head,
            to: editor.state.selection.main.head,
            insert: '<img src="' + fileReader.result + '"/>'
          }
        });
      });
    };

    controls.append(editorSave, editorLoadImg);
  });

  eventBus.on('element.setCode', function (_, element, content) {
    saveModalEditor(element, content);
  });

  eventBus.on('codeEdit.save', function (_, element, value) {
    saveModalEditor(element, value);
  });

  function saveModalEditor (element, newValue) {
    if (!element.innerLabel || !element.innerLabel.isHtml || !newValue) {
      element.innerLabel = { isHtml: !!newValue };
      //     html 
      const gfx = elementRegistry.getGraphics(element);
      eventBus.fire('interactionEvents.removeHit', { gfx });
      eventBus.fire('interactionEvents.createHit', { element: element, gfx });
    }

    element.innerLabel.text = newValue;

    eventBus.fire('element.labelChanged', { element: element });
    eventBus.fire('element.changed', { element: element });
  }
}

CodeEdit.$inject = [
  'eventBus',
  'elementRegistry'
];
