/**
 *      
 */
export default function CustomCommandsMenuProvider (popupMenu) {
  this._popupMenu = popupMenu;
  this._dynamicEntries = [];
  this._dynamicHeaderEntries = [];

  this.register();
}

CustomCommandsMenuProvider.$inject = ['popupMenu'];

/**
 *       
 * @param {array} newEntries
 */
CustomCommandsMenuProvider.prototype.addEntries = function (newEntries) {
  const ids = newEntries.map(entry => entry.id);
  this._dynamicEntries = this._dynamicEntries
    .filter(entry => !ids.includes(entry.id))
    .concat(newEntries);
};

CustomCommandsMenuProvider.prototype.addHeaderEntries = function (newHeaderEntries) {
  const ids = newHeaderEntries.map(entry => entry.id);
  this._dynamicHeaderEntries = this._dynamicHeaderEntries
    .filter(entry => !ids.includes(entry.id))
    .concat(newHeaderEntries);
};

/**
 *   
 *
 * @param {djs.model.Base} element
 */
CustomCommandsMenuProvider.prototype.getEntries = function (element) {
  const entries = [
  ].concat(this._dynamicEntries);

  return entries;
};

/**
 *   .
 *
 * @param  {djs.model.Base} element
 */
CustomCommandsMenuProvider.prototype.getHeaderEntries = function (element) {
  const headerEntries = [
  ].concat(this._dynamicHeaderEntries);

  return headerEntries;
};

/**
 *    id.
 */
CustomCommandsMenuProvider.prototype.register = function () {
  this._popupMenu.registerProvider('custom-commands-menu', this);
};
