import { basicSetup, EditorView } from 'codemirror';
import { html } from '@codemirror/lang-html';

/**
   *    
   * @param  {eventBus}  eventBus
   */
export default function Code (eventBus) {
  this._eventBus = eventBus;

  eventBus.on('code.open', function (_, title, value) {
    return openModalEditor(title, value);
  });

  eventBus.on('code.close', function (_, modal) {
    eventBus.fire('modal.close', modal);
  });

  function openModalEditor (title, value) {
    //    
    const { modal, header, body, controls } = eventBus.fire('modal.open');
    header.classList.add('cm-editor-header');

    const editorTitle = document.createElement('span');
    editorTitle.innerText = title;
    editorTitle.className = 'cm-editor-title';
    header.prepend(editorTitle);

    const editorContainer = document.createElement('div');
    editorContainer.className = 'cm-editor-container';

    body.classList.add('cm-editor-body');
    body.appendChild(editorContainer);

    const codeEditor = new EditorView({
      parent: editorContainer,
      doc: value,
      lineNumbers: true,
      extensions: [
        basicSetup,
        EditorView.lineWrapping,
        html()
      ]
    });

    return {
      editor: codeEditor,
      container: {
        modal, header, body, controls
      }
    };
  }
}

Code.$inject = [
  'eventBus'
];
