import CommandModule from 'diagram-js/lib/command';
import TooltipsModule from 'diagram-js/lib/features/tooltips';
import LabelSupportModule from 'diagram-js/lib/features/label-support';
import AttachSupportModule from 'diagram-js/lib/features/attach-support';
import SelectionModule from 'diagram-js/lib/features/selection';
import ChangeSupportModule from 'diagram-js/lib/features/change-support';
import SpaceToolModule from 'diagram-js/lib/features/space-tool';

import CustomLayouter from './CustomLayouter';
import CroppingConnectionDocking from 'diagram-js/lib/layout/CroppingConnectionDocking';
import ElementFactory from './ElementFactory';
import ElementUpdater from './ElementUpdater';
import Modeling from './Modeling';

export default {
  __init__: [
    'modeling',
    'elementUpdater'
  ],
  __depends__: [
    CommandModule,
    TooltipsModule,
    LabelSupportModule,
    AttachSupportModule,
    SelectionModule,
    ChangeSupportModule,
    SpaceToolModule
  ],
  modeling: ['type', Modeling],
  elementFactory: ['type', ElementFactory],
  elementUpdater: ['type', ElementUpdater],
  layouter: ['type', CustomLayouter],
  connectionDocking: ['type', CroppingConnectionDocking]
};
