import { assign } from 'min-dash';

/**
 * A example context pad provider.
 */
export default function ExampleContextPadProvider (connect, contextPad, modeling, eventBus, copyPaste, popupMenu) {
  this._connect = connect;
  this._modeling = modeling;
  this._eventBus = eventBus;
  this._copyPaste = copyPaste;
  this._contextPad = contextPad;
  this._popupMenu = popupMenu;

  contextPad.registerProvider(this);
}

ExampleContextPadProvider.$inject = [
  'connect',
  'contextPad',
  'modeling',
  'eventBus',
  'copyPaste',
  'popupMenu'
];

ExampleContextPadProvider.prototype.getContextPadEntries = function (element) {
  const connect = this._connect;
  const modeling = this._modeling;
  const eventBus = this._eventBus;
  const copyPaste = this._copyPaste;
  const contextPad = this._contextPad;
  const popupMenu = this._popupMenu;

  function removeElement () {
    modeling.removeElements([element]);
  }

  function startConnect (event, element, autoActivate) {
    connect.start(event, element, autoActivate);
  }

  /**
   *     
   * .  edit ,      DirectEditing
   *     Label   Hint
   * @param {Object} event;
   * @param {Object} element;
   */
  function startHintEdit (event, element) {
    eventBus.fire('element.hintEdit', { element, edit: 'hint' });
  }

  /**
   *       
   * @param {Object} event;
   * @param {Object} element;
   */
  function startCodeEdit (event, element) {
    eventBus.fire('element.codeEdit', element);
  }

  /**
   *       
   * @param {Object} event;
   * @param {Object} element;
   */
  function uploadImage (event, element) {
    eventBus.fire('element.imageUpload', (fileReader) => {
      const imgElement = '<img src="' + fileReader.result + '"/>';
      eventBus.fire('element.setCode', element, imgElement);
    });
  }

  /**
   *     
   * @param {Object} event;
   * @param {Object} element;
   */
  function cloneElement (event, element) {
    copyPaste.copy(element);
    copyPaste.paste();
  }

  function getReplaceMenuPosition (element) {
    const Y_OFFSET = 5;
    const pad = contextPad.getPad(element).html;
    const padRect = pad.getBoundingClientRect();
    const pos = {
      x: padRect.left,
      y: padRect.bottom + Y_OFFSET
    };

    return pos;
  }

  const toolsShape = {
    delete: {
      group: 'edit',
      className: 'context-pad-icon-remove',
      title: '',
      action: {
        click: removeElement,
        dragstart: removeElement
      }
    },
    connect: {
      group: 'edit',
      className: 'context-pad-icon-connect',
      title: '',
      action: {
        click: startConnect,
        dragstart: startConnect
      }
    },
    copy: {
      group: 'label',
      className: 'context-pad-icon-copy',
      title: '',
      action: {
        click: cloneElement,
        dragstart: cloneElement
      }
    },
    ...(!element.isFrame && element.editable !== false && {
      hint: {
        group: 'label',
        className: 'context-pad-icon-hint',
        title: ' ',
        action: {
          click: startHintEdit
        }
      },
      code: {
        group: 'code',
        className: 'context-pad-icon-code',
        title: ' ',
        action: {
          click: startCodeEdit
        }
      },
      image: {
        group: 'code',
        className: 'context-pad-icon-img',
        title: ' ',
        action: {
          click: uploadImage
        }
      }
    }),
    ...(element.commands && {
      commands: {
        name: 'element-commands',
        group: 'tools',
        className: 'context-pad-icon-commands',
        title: ' ',
        action: {
          click: function (event, element) {
            const position = assign(getReplaceMenuPosition(element), {
              cursor: { x: event.x, y: event.y }
            });

            popupMenu.open(element, 'element-commands-menu', position);
          }
        }
      }
    })
  };

  const toolsConnection = {
    delete: {
      group: 'edit',
      className: 'context-pad-icon-remove',
      title: '',
      action: {
        click: removeElement,
        dragstart: removeElement
      }
    },
    hint: {
      group: 'hint',
      className: 'context-pad-icon-hint',
      title: ' ',
      action: {
        click: startHintEdit,
        dragstart: startHintEdit
      }
    }
  };

  if (element.type === 'shape') {
    if (element.tools) {
      return filterTools(toolsShape, element.tools);
    }
    return toolsShape;
  }

  if (element.tools) {
    return filterTools(toolsConnection, element.tools);
  }
  return toolsConnection;
};

function filterTools (tools, allowed) {
  return Object.keys(tools).reduce((filteredTools, toolName) => {
    if (allowed.includes(toolName)) {
      filteredTools[toolName] = tools[toolName];
      return filteredTools;
    }
    return filteredTools;
  }, {});
}
