import inherits from 'inherits';
import {
  forEach
} from 'min-dash';

import BaseResizeHandles from 'diagram-js/lib/features/resize/ResizeHandles';

export default function ResizeHandles (eventBus, canvas, selection, resize) {
  BaseResizeHandles.call(this, eventBus, canvas, selection, resize);
}

ResizeHandles.$inject = [
  'eventBus',
  'canvas',
  'selection',
  'resize'
];

inherits(ResizeHandles, BaseResizeHandles);

const sides = ['n', 'w', 's', 'e'];
const diagonal = ['nw', 'ne', 'se', 'sw'];
ResizeHandles.prototype.addResizer = function (shape) {
  const self = this;

  const resize = this._resize;

  if (!resize.canResize({ shape: shape })) {
    return;
  }

  let directions;
  if (shape.subType === 'circle') {
    directions = diagonal;
  } else {
    directions = sides.concat(diagonal);
  }

  forEach(directions, function (direction) {
    self.createResizer(shape, direction);
  });
};
